import miLoading from '../../components/mi-loading/mi-loading.vue' // loading动画
import uniFab from '@/components/uni-fab/uni-fab.vue';
import uniPopup from '@/components/uni-popup/uni-popup.vue' // 弹出层
export default {
	components: {
		uniFab,miLoading, uniPopup
	},
	onLoad(option) {
		console.log(option)
		this.type = option.type
		var that = this
		// 获取本地存储的图片
		uni.getStorage({
			key: 'title',
			success: function(res) {
				that.titleM = res.data
			}
		});
	},
	data() {
		return {
			webviewStyles: {
				progress: {
					color: 'red'
				},
				height: '70%'
			},
			type: '', //电影连接
			playInfo: true, //电影播放
			titleM: '',
			title: 'uni-fab',
			horizontal: 'left',
			vertical: 'bottom',
			direction: 'horizontal',
			pattern: {
				color: '#7A7E83',
				backgroundColor: '#fff',
				selectedColor: '#007AFF',
				buttonColor: '#007AFF'
			},
			content: [{
					iconPath: '/static/img/reset.png',
					selectedIconPath: '/static/img/reset.png',
					text: '刷新',
					id: 1,
					active: false
				},
				{
					iconPath: '/static/img/help.png',
					selectedIconPath: '/static/img/help.png',
					text: '	关于',
					id: 2,
					active: false
				}
			]
		}
	},
	methods: {
		trigger(item) {
			var that = this
			console.log(item.item.id)
			if(item.item.id === 1){
				that.playInfo = false
				setTimeout(() => {
					that.$refs.loading.show() // 打开
				}, 10)
				setTimeout(() =>{
					that.playInfo = true
					that.$refs.loading.hide() // 打开
				},1000)
			}else {
				that.$refs.popup.open()
			}
			console.log(index)
		}
	},
}
